.OpenModal {
  border-top:5px solid black;
  border-bottom:5px solid black;
  border-left: 5px solid black;
  border-right: 0px;
  background-color: #61DAFA;
  cursor: pointer;
  font-size: 24px;
  font-family: 'Montserrat';
  font-weight: bold;
  color:black;
  display: inline;
  justify-content: center;
  align-items: center;
  width: 21.1%;
  height: 65px;
  margin-left: -2px;
  margin-right: -2px;
}

.OpenModal:hover {
  background-color: red;
}
