.TodoSearch {
  background: #F9FBFC;
  border-radius: 2px;
  border-top: 5px solid black;
  border-bottom: 5px solid black;
  height: 55px;
  width: 80%;
  margin-left: -4px;
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61DAFA;
}
